import axios from 'axios'
export default {
  props: {
    gallery: {
      required: true,
      type: Array,
    },
  },
  data() {
    return {
      imgData: null,
    };
  },
  methods: {
    linkGen(pageNum) {
      return pageNum === 1 ? "?" : `/photos/${pageNum}`;
    },

    closed() {
      this.imgData = null;
    },

    download(img) {
      axios({
        method: 'get',
        url: img.url,
        responseType: 'blob',
      }).then((response) => {
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement('a');
        link.href = url;
        link.setAttribute('download', img.name || 'image');
        document.body.appendChild(link);
        link.click();
      });
    },

    next() {
      const findIndex = this.gallery.findIndex(
        (item) => item === this.imgData
      );
      this.imgData =
        this.gallery[
          findIndex < this.gallery.length - 1 ? findIndex + 1 : 0
        ];
    },

    prev() {
      const findIndex = this.gallery.findIndex(
        (item) => item === this.imgData
      );
      this.imgData =
        this.gallery[
          findIndex === 0 ? this.gallery.length - 1 : findIndex - 1
        ];
    },
  },
};
